/* M A I N   P A G E   S T Y L I N G */
@import url('https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Poppins:wght@200;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Clicker+Script&display=swap');

* {
  box-sizing: border-box;
  margin: auto;
  z-index: 0;
   font-family: 'Raleway', sans-serif;
  //  font-family: 'poppins', sans-serif; 
  scroll-behavior: smooth;
  overflow-x: hidden;
  
}


h2 {
  margin-top: 50px;
  font-size: 40px;
  font-weight: 100;
  // text-align: center;
}

/* H E A D E R   S T Y L I N G */

.header {
  background-color: #1c1d25;
  height: 100vh;
  top: 0;
  z-index: -1;
  text-align: center;
  color: white;
  display: flex;
  justify-content: center;
  text-align: center;
} 

/* Navbar styling */

nav {
  background-color: #1c1d25;
  color: white;
  display: flex;
  justify-content: space-between;
  font-size: 40px;
  position: fixed;
  z-index: 2;
  width: 100%;
  padding: auto;
  height: 60px;

}
 .logo{
  margin-right: auto;
    margin-left:0 - 1px;
    padding-left: 20px;
}
nav ul li a{
  color: white;
  
}
nav ul{
  display: flex;
  justify-content: space-between;
  font-size: 25px;
  margin-left: auto;
  margin-right: -20px;
}
nav ul li{
   width: 180px;
}
.title{
  float: left;
  padding: auto;
  margin-left: 100px;
  margin-right: 450px;
  text-align: left;
}
.button{
  width: 220px;
  height: 60px;
  border-radius: 30px;
  background-color:coral;
  text-align: center;
  align-items: center;
  margin-top: 10px;
  padding-top:5px;
  float: left;

}
.button:hover{
  background-color: orangered;
}


h1 {
  font-size:90px;
  font-weight: 80;
  // letter-spacing: 1px;
}
.title p {
   font-size: 40px;
  font-weight: 100;
  // letter-spacing: 3px;
}

.logo-container{
  display: flex;
  flex-direction: column;
  margin-right: 60px;
}


a {
  text-decoration: none;
  color:white;
}

.link-icon {
   margin: 30px;
}
