.modal {
  display: none; 
  position: fixed; 
  z-index: 1;
  overflow: hidden;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh; 
  background-color: rgb(0,0,0); 
  background-color: rgba(0,0,0,0.5); 
}

.modal-textbox {
  border-top: coral solid;
  border-width: 5px;
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  width: 75%; 
  text-align: center;
  overflow: hidden;
}

.collapse {
  color: #aaa;
  float: right;
  font-size: 15px;
  text-decoration: underline;
}

.collapse:hover {
  color: black;
  cursor: pointer;
}


