@media only screen and (max-width: 800px) {
  .portfolio-container {
    margin-top: 50px;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .project-container {
    width: 99%;
    margin-bottom: 10vh; /* (100-32*3)/2 */
    position: relative;
    display: block;
  }
  
  .project-preview-img{
    height: auto;
    width: 98%;
  }
  .left-side {
    display: block;
    width: 100%;
  }
  
  .right-side {
    display: block;
    width: 98%;
    margin-left: 1%;
  }
  
  .right-side p {
    line-height: 1;
    height: auto;
    margin: 10px 0px;
  }
  .portfolio-link {
    font-size: 15px;
    font-weight: 100;
    z-index: 0;
  }
  
  .right-side h5 {
    font-size: 30px;
  }

}
@media only screen and (max-width: 500px) {


